.dialogsBlock {
  min-height: 100vh;

  .containerMess {
    /*height: 100%;*/
    height: 100%;
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    .dialogsItems {
      width: 20%;
    }

    .messages {
      width: 70%;
      min-height: 80%;
      display: flex;
      flex-direction: column;

      .messTextBlock {
        /*min-height: 60%;*/
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background-color: white;
        border-radius: .8em .8em 1.3em 1.3em;
      }

      div {
        textarea {
          border-radius: 1.3em;
          box-shadow: 10px 10px 7px teal;
          margin-top: 10px;
          width: 95%;
          font-size: 16pt;
          overflow: hidden;
          padding: 10px;
        }
      }

      button {
        box-shadow: 3px 4px 0 0 #1564ad;
        background: #79bbff linear-gradient(to bottom, #79bbff 5%, #378de5 100%);
        border-radius: 5px;
        border: 1px solid #337bc4;
        display: inline-block;
        cursor: pointer;
        color: #ffffff;
        font-size: 17px;
        font-weight: bold;
        padding: 9px 17px;
        text-decoration: none;
        text-shadow: 0 1px 0 #528ecc;
        width: 75px;
        margin: 15px 0 0 10px;

        &:hover {
          background: #378de5 linear-gradient(to bottom, #378de5 5%, #79bbff 100%);
        }

        &:active {
          position: relative;
          top: 1px;
        }
      }
    }
  }
}
